import React, { Fragment, useEffect, useState } from "react";

// component
import MinistryCards from "../components/CardsMinistry.js";
import { Container, Row, Col } from "react-bootstrap";
import { isMobile, isTablet } from "react-device-detect";
import { v4 as uuidv4 } from "uuid";
import { useForm } from "react-hook-form";
import { EmptySearch } from "../components/Universal/SearchBar";
import { ScaffoldComponent } from "../components/Universal/Scaffold";
import { fetchData } from "../components/Functions/PageFunctions";

export default function PageMinistries() {
  const { register, handleSubmit } = useForm();
  const [dataList, setDataList] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const itemsPerPage = 6;
  const category = "ministry";

  const setAll = (dataList, currentPage, totalPage) => {
    setDataList(dataList);
    setCurrentPage(currentPage);
    setTotalPages(totalPage);
  };

  const onSubmit = (keyword) => {
    fetchData(category, keyword.Search, currentPage, itemsPerPage).then(
      (getData) => {
        const result = getData.data.result;
        setAll(
          result,
          result.current_page,
          Math.ceil(result.total / itemsPerPage)
        );
      }
    );
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
    fetchData(category, "", page, itemsPerPage).then((getData) => {
      const result = getData.data.result;
      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  };

  useEffect(() => {
    fetchData(category, "", currentPage, itemsPerPage).then((getData) => {
      const result = getData.data.result;

      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  }, [currentPage]);

  const CardComponent = () => {
    return dataList.data?.length !== 0 ? (
      <Container
        fluid
        className="d-flex justify-content-center h-auto m-0 p-0 mt-5"
      >
        <Row className={`m-0 p-0 ${isTablet || isMobile ? "w-100" : "w-75"}`}>
          {dataList.data?.map((props) => {
            return (
              <MinistryCards data={props} key={uuidv4()} onSubmit={onSubmit} />
            );
          })}
        </Row>
      </Container>
    ) : (
      <EmptySearch />
    );
  };

  return (
    <Fragment>
      <Container fluid className="m-0 p-0 w-100">
        <Row
          lg={12}
          className={`page-divider m-0 p-0 ${isMobile ? "w-100" : "w-75"} h-25`}
        >
          <Col lg={12} className="h-100 align-items-center d-flex">
            <h1
              className={`page-divider-text p-5 ${
                isMobile ? "text-center" : ""
              }`}
            >
              Our Ministries
            </h1>
          </Col>
        </Row>
      </Container>
      <ScaffoldComponent
        dataList={dataList}
        onSubmit={onSubmit}
        register={register}
        handleSubmit={handleSubmit}
        totalPages={totalPages}
        currentPage={currentPage}
        handlePageChange={handlePageChange}
        CardComponent={CardComponent}
      />
    </Fragment>
  );
}
