import React from "react";

//component
import { Col, Card, Button, Container, Badge } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";

export default function CardsMinistry({ data, onSubmit }) {
  const navigate = useNavigate();

  const ministryGoTo = (e) => {
    e.preventDefault();
    navigate("/Ministries/MinistryDetails", { state: data });
  };

  return (
    <>
      <Col
        xs={12}
        lg={4}
        md={4}
        className="p-2 d-flex justify-content-center my-2"
      >
        <Card
          style={{ width: "25rem", borderRadius: "0px" }}
          className="ministry-card"
        >
          <Card.Img
            style={{ borderRadius: "0px" }}
            variant="top"
            src={
              process.env.REACT_APP_API_URL +
              "/images/contents/" +
              data?.content_thumbnail
            }
          />
          <Card.Body className="d-flex flex-column">
            <Card.Title className="ministry-text-list-title">
              {data?.content_title}
            </Card.Title>
            <Container fluid className="p-2">
              {data?.content_tags?.map((tags) => {
                return (
                  <Badge
                    className="pill-cursor"
                    bg="primary"
                    pill
                    key={uuidv4()}
                    onClick={() => onSubmit({ Search: tags })}
                  >
                    {tags}
                  </Badge>
                );
              })}
            </Container>
            <Container
              dangerouslySetInnerHTML={{
                __html: data?.content_description.sub_content_section,
              }}
            ></Container>
            <Button
              variant="primary"
              onClick={(e) => ministryGoTo(e)}
              className="mt-auto"
            >
              View Details
            </Button>
          </Card.Body>
        </Card>
      </Col>
    </>
  );
}
